.banner {
  position: relative;
  z-index: 2; }

.banner:after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background: #17222B;
  opacity: .6;
  left: 0;
  top: 0;
  z-index: -1; }

.inner_overlay {
  position: relative;
  z-index: 1; }

.inner_overlay:after {
  position: absolute;
  z-index: -1;
